        user_memo:'',
        users: [] // 새로운 배열 속성 추가
    }),

  created() {
      axios.get('http://localhost:8080/api/v1/users/user/getAllUsers',
          {
            headers: {
              Authorization: localStorage.getItem('Authorization')
            }
          })
          .then((res) => {
            if (res) {
            console.log('created() res.data >> ', res);
            this.users = res.data.users;

            console.log('this.data>>', this.data);
            console.log('성공함...');

            }
          })
          .catch((err) => {
            console.log('err>>', err);
            this.loginError = true;
            throw new Error(err);
          });

  },

  computed: {
    filteredUsers() {
      return this.users.filter(user => user.roles.includes('ROLE_ADMIN'));
    }
  },

  methods: {
    excelDownload() {
      console.log('excelDownload 클릭됨..')

      axios.get('http://localhost:8080/api/v1/users/excel/download',

          { // 8080/login은 아예 컨트럴러 안 탐.
            headers: {
              Authorization: localStorage.getItem('Authorization'),
            },
          })
          .then((res) => {
            if (res.status === 200) {


            }
          })
          .catch((err) => {
            // loginError.value = true;
            console.error(err);
          });
    },

    handleUserClick(user) {
      // 클릭된 span 태그의 데이터 속성을 확인
      const employeeNo = user.employee_no;
      console.log(`Span with employee_no ${employeeNo} is clicked!`);

      axios.post('http://localhost:8080/api/v1/users/user/showNotePad',
          {
            employee_no: user.employee_no
          },
          { // 8080/login은 아예 컨트럴러 안 탐.
            headers: {
              Authorization: localStorage.getItem('Authorization'),
            },
          })
          .then((res) => {
            if (res.status === 200) {
              console.log(' res.data.memo >> ', res.data.memo);

            }
          })
          .catch((err) => {
            // loginError.value = true;
            console.error(err);
          });
    },

    navigateToPwChange() {
      this.$router.push({ name: 'pw-change' });
    },

      /* 비밀번호 버튼 클릭 이벤트 */
      async pwChange() {
        console.log(' pwd-number>>', this.pwd_number);

        await axios.post('http://localhost:8080/api/v1/users/user/update',{
              name: this.user_name,
              password: this.pwd_number,
            },
            {
              headers: {
                Authorization: localStorage.getItem('Authorization')
              }
            })
            .then((res) => {
                console.log('result.data >> ', res);
              if (res.data === 'ok') {
                console.log('성공함...');


                //this.loginSuccess = true;

                // Call the users method here
                //this.users();
              }
            })
            .catch((err) => {
              this.loginError = true;
              throw new Error(err);
            });
      }, // login()

        // show() {
        //     $(".pwChange").css('display', 'block');
